<template>
  <el-form
    :model="ruleForm"
    status-icon
    :rules="rules"
    ref="ruleForm"
    label-width="80px"
    class="login_container"
  >
    <h3 class="login_title">系统登陆</h3>
    <el-form-item label="用户名" prop="username">
      <el-input
        type="input"
        v-model="ruleForm.username"
        autocomplete="off"
      ></el-input>
    </el-form-item>

    <el-form-item label="密码" prop="password">
      <el-input
        type="password"
        v-model="ruleForm.password"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <el-form-item class="login_submit">
      <el-button type="primary" @click="login" class="login_submit"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import { getMenu } from "../../api/data";
export default {
  name: "logIn",
  data() {
    return {
      ruleForm: {
        username: "",
        password: "",
      },
      rules: {
        name: [
          { required: true, message: "输入用户名", trigger: "blur" },
          { min: 3, message: "用户名长度不能小于 3 个字符", trigger: "blur" },
        ],
        pass: [{ required: true, message: "输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    login() {
      getMenu(this.ruleForm).then((res) => {
        console.log('res',res);
        if (res.code === 20000) {
          this.$store.commit("tab/clearMenu");
          this.$store.commit("tab/setMenu", res.data.menu);
          this.$store.commit("user/setToken", res.data.token);
          console.log(res, res.data.menu, 44);
          this.$store.commit("tab/addMenu", this.$router);
          this.$router.push({ name: 'home'});
        } else {
          this.$message.warning(res.data.message);
        }
      });
      //随机值
      /*   const token = Mock.Random.guid();
      this.$store.commit("setToken", token);
      this.$router.push({ name: "home" }); */
    },
  },
};
</script>

<style  scoped>
.login_container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 30px 35px 15px 35px;
  background-color: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login_submit {
  margin: 10px auto 0px auto;
}
</style>